
/* /website_mail/static/src/css/website_mail.scss */

.js_follow[data-follow='on'] .js_follow_btn,
.js_follow[data-follow='off'] .js_unfollow_btn {
  display: none;
}

.js_follow_icons_container .js_follow_btn, .js_follow_icons_container .js_unfollow_btn {
  animation: js_follow_fade 1s ease forwards;
  opacity: 0;
}

.js_follow_icons_container .js_follow_btn small, .js_follow_icons_container .js_follow_btn .small, .js_follow_icons_container .js_unfollow_btn small, .js_follow_icons_container .js_unfollow_btn .small {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.js_follow_icons_container .js_follow_btn:hover small, .js_follow_icons_container .js_follow_btn:hover .small, .js_follow_icons_container .js_follow_btn:focus small, .js_follow_icons_container .js_follow_btn:focus .small, .js_follow_icons_container .js_unfollow_btn:hover small, .js_follow_icons_container .js_unfollow_btn:hover .small, .js_follow_icons_container .js_unfollow_btn:focus small, .js_follow_icons_container .js_unfollow_btn:focus .small {
  transition-duration: 1s;
  opacity: 1;
}

.js_follow_icons_container .fa:before {
  content: "\f0f3";
  color: rgba(33, 37, 41, 0.7);
}

.js_follow_icons_container .js_follow_btn:hover .fa:before {
  color: #212529;
}

.js_follow_icons_container .js_unfollow_btn .fa:before {
  color: #6772E5;
}

.js_follow_icons_container .js_unfollow_btn:hover .fa:before {
  content: "\f1f6";
  color: #dc3545;
}

@keyframes js_follow_fade {
  to {
    opacity: 1;
  }
}

