
/* /web_editor/static/src/scss/web_editor.frontend.scss */

@media (max-width: 767.98px) {
  img:not(.o_animate), .media_iframe_video:not(.o_animate), span.fa:not(.o_animate), i.fa:not(.o_animate) {
    transform: none !important;
  }
}

.o_wysiwyg_loader {
  pointer-events: none;
  min-height: 100px;
  color: transparent;
}

.o_wysiwyg_loading {
  position: absolute;
  top: 50%;
  left: 50%;
  bottom: auto;
  right: auto;
  transform: translate(-50%, -50%);
}

@media (max-width: 767.98px) {
  .o_we_shape {
    display: none;
  }
}

.o_we_flip_x {
  transform: scaleX(-1);
}

.o_we_flip_y {
  transform: scaleY(-1);
}

.o_we_flip_x.o_we_flip_y {
  transform: scale(-1);
}

.o_grid_mode {
  --gutter-x: 0px;
  --grid-item-padding-y: 10px;
  --grid-item-padding-x: 10px;
}

@media (min-width: 992px) {
  .o_grid_mode {
    display: grid !important;
    grid-auto-rows: 50px;
    grid-template-columns: repeat(12, 1fr);
    row-gap: 0px;
    column-gap: 0px;
  }
}

.o_grid_mode > * {
  width: 100%;
  min-width: 0;
  margin: 0 !important;
  padding: var(--grid-item-padding-y) var(--grid-item-padding-x) !important;
}

.container-fluid > .o_grid_mode, .container-sm > .o_grid_mode, .container-md > .o_grid_mode, .container-lg > .o_grid_mode, .container-xl > .o_grid_mode, .container-xxl > .o_grid_mode {
  --gutter-x: 30px;
}

.o_grid_item_image > img, .o_grid_item_image > .media_iframe_video {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
}

.o_grid_item_image.o_grid_item_image_contain > img,
.o_grid_item_image > img[data-shape] {
  object-fit: contain !important;
}

body.editor_enable:not(.o_basic_theme) .odoo-editor-editable img::selection {
  background-color: transparent !important;
}

