
/* /website_sale/static/src/scss/product_configurator.scss */

.css_attribute_color {
  position: relative;
  display: inline-block;
  border: 5px solid #CED4DA;
  border-radius: 50%;
  text-align: center;
  transition: background-color 0.05s ease-in-out, border-color 0.05s ease-in-out, box-shadow 0.05s ease-in-out;
}

.css_attribute_color .form-check-input:not(:disabled):not(.o_wysiwyg_loader), .css_attribute_color .form-check-input:not(:disabled):not(.o_wysiwyg_loader) + label {
  cursor: pointer;
}

.css_attribute_color:hover, .css_attribute_color:hover .form-check-input:not(:disabled):not(.o_wysiwyg_loader) {
  border-color: #6772E5;
}

.css_attribute_color:before {
  content: "";
  display: block;
  position: absolute;
  top: -3px;
  left: -3px;
  bottom: -3px;
  right: -3px;
  border: 4px solid white;
  border-radius: 50%;
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}

.css_attribute_color input {
  margin: 8px;
  height: 13px;
  width: 13px;
  opacity: 0;
}

.css_attribute_color.active {
  border: 5px solid #6772E5;
}

.css_attribute_color.custom_value {
  background-image: linear-gradient(to bottom right, #FF0000, #FFF200, #1E9600);
}

.css_attribute_color.transparent {
  background-image: url(/web/static/img/transparent.png);
}

.css_not_available_msg {
  display: none;
}

.css_not_available.js_product .css_quantity {
  display: none !important;
}

.css_not_available.js_product .css_not_available_msg {
  display: block;
}

.css_not_available.js_product .js_add,
.css_not_available.js_product .oe_price,
.css_not_available.js_product .oe_default_price,
.css_not_available.js_product .oe_optional {
  display: none;
}

.css_quantity {
  width: initial;
}

.css_quantity .btn, .css_quantity input {
  border-color: #CED4DA;
}

.css_quantity input {
  height: auto !important;
  max-width: 5ch;
}

option.css_not_available {
  color: #ccc;
}

select.form-select.css_attribute_select {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='175' height='100' fill='theme-color(\"primary\")'><polygon points='0,0 100,0 50,50'/></svg>");
  background-size: 20px;
  background-position: 100% 65%;
  background-repeat: no-repeat;
  max-width: 400px;
}

label.css_not_available, .o_variant_pills.css_not_available {
  opacity: 0.6;
}

label.css_attribute_color.css_not_available {
  opacity: 1;
}

label.css_attribute_color.css_not_available:after {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  bottom: -5px;
  right: -5px;
  border: 2px solid #dc3545;
  border-radius: 50%;
  background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='39' height='39'><line y2='0' x2='39' y1='39' x1='0' style='stroke:%23dc3545;stroke-width:2'/><line y2='1' x2='40' y1='40' x1='1' style='stroke:rgb(255,255,255);stroke-width:1'/></svg>");
  background-position: center;
  background-repeat: no-repeat;
}

.radio_input_value .badge, select .badge, label .badge {
  margin-left: 3px;
  padding-left: 3px;
}

.radio_input_value .sign_badge_price_extra, select .sign_badge_price_extra, label .sign_badge_price_extra {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  color: #6772E5;
  background: white;
  line-height: 1rem;
  border-radius: 50%;
}

.variant_attribute {
  padding-bottom: 1rem;
}

.variant_attribute .attribute_name {
  display: -webkit-box; display: -webkit-flex; display: flex;
  align-items: center;
  font-size: 0.9rem;
  text-transform: uppercase;
  padding-bottom: 0.5rem;
}

.variant_attribute .attribute_name:after {
  content: '';
  margin-left: 1rem;
  flex-grow: 1;
  border-bottom: 1px solid #CED4DA;
}

.variant_attribute .radio_input_value {
  font-weight: 400;
}

.variant_attribute .radio_input_value:not(.o_variant_pills_input_value) {
  margin-right: 1rem;
}

.variant_attribute .radio_input_value:not(.o_variant_pills_input_value), .variant_attribute .radio_input_value:not(.o_variant_pills_input_value) > span {
  vertical-align: middle;
}

.variant_attribute .radio_input_value.o_variant_pills_input_value .badge {
  color: #6C757D;
  background: white;
  border: 1px solid #6772E5;
}

.variant_attribute .radio_input_value.o_variant_pills_input_value .badge, .variant_attribute .radio_input_value.o_variant_pills_input_value .badge > span {
  vertical-align: middle;
}

.variant_attribute .radio_input_value.o_variant_pills_input_value .badge .sign_badge_price_extra {
  font-size: 1.1rem;
}

.variant_attribute .variant_custom_value {
  margin-bottom: 0.7rem;
}

.variant_attribute .variant_custom_value.custom_value_radio {
  display: inline-block;
}

.variant_attribute select {
  margin-bottom: 0.5rem;
}

.variant_attribute ul.list-inline {
  margin-left: 0;
}

.variant_attribute .o_variant_pills {
  padding: 0.5rem 1rem;
  margin-right: 0.2rem;
  border: none;
  cursor: default !important;
}

.variant_attribute .o_variant_pills.btn.active {
  background-color: #6772E5;
}

.variant_attribute .o_variant_pills:not(.active) {
  color: #6C757D;
  background-color: #E9ECEF;
}

.variant_attribute .o_variant_pills input {
  -moz--webkit-appearance: none; -moz-appearance: none; appearance: none;
  -webkit--webkit-appearance: none; -moz-appearance: none; appearance: none;
  -webkit-appearance: none; -moz-appearance: none; appearance: none;
  opacity: 0;
}

.o_product_configurator .product_detail_img {
  max-height: 240px;
}

.table-striped tbody tr:nth-of-type(odd) .o_select_options {
  background-color: rgba(0, 0, 0, 0.025);
}

.table-striped tbody tr:nth-of-type(odd) .o_total_row {
  font-size: 1.2rem;
}

.modal.o_technical_modal .oe_advanced_configurator_modal .btn.js_add_cart_json {
  padding: 0.075rem 0.75rem;
}

.js_product .td-product_name {
  word-wrap: break-word;
}

.js_product .td-product_name {
  min-width: 140px;
}

.js_product .td-img {
  width: 100px;
}

.js_product .td-qty {
  width: 200px;
}

.js_product .td-qty a.input-group-addon {
  background-color: transparent;
  border: 0px;
}

.js_product .td-qty .input-group {
  display: -webkit-inline-box; display: -webkit-inline-flex; display: inline-flex;
}

.js_product .td-action {
  width: 30px;
}

.js_product .td-price,
.js_product .td-price-total {
  width: 120px;
}

@media (max-width: 767.98px) {
  .js_product .td-img,
  .js_product .td-price-total {
    display: none;
  }
  .js_product .td-qty {
    width: 60px;
  }
  .js_product .td-price {
    width: 80px;
  }
}

@media (max-width: 476px) {
  .js_product .td-qty {
    width: 60px;
  }
  .js_product #modal_optional_products table thead,
  .js_product .oe_cart table thead {
    display: none;
  }
  .js_product #modal_optional_products table td.td-img,
  .js_product .oe_cart table td.td-img {
    display: none;
  }
}

.o_total_row {
  height: 50px;
}

.oe_striked_price {
  text-decoration: line-through;
  white-space: nowrap;
}

