
/* /web/static/src/legacy/scss/utils.scss */

.o_colorpicker_widget .o_opacity_slider, .o_colorpicker_widget .o_color_preview, .modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_cell.o_we_image .o_we_media_dialog_img_wrapper {
  position: relative;
  z-index: 0;
}

.o_colorpicker_widget .o_opacity_slider::before, .o_colorpicker_widget .o_color_preview::before, .modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_cell.o_we_image .o_we_media_dialog_img_wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background-image: url("/web/static/img/transparent.png");
  background-size: 10px auto;
  border-radius: inherit;
}

.o_colorpicker_widget .o_opacity_slider::after, .o_colorpicker_widget .o_color_preview::after, .modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_cell.o_we_image .o_we_media_dialog_img_wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  background: inherit;
  border-radius: inherit;
}

.o_nocontent_help .o_empty_folder_image:before, .o_knowledge_form_view .o_view_nocontent_empty_folder:before {
  content: "";
  display: block;
  margin: auto;
  background-size: cover;
}

.o_nocontent_help .o_empty_folder_image:before, .o_knowledge_form_view .o_view_nocontent_empty_folder:before {
  width: 120px;
  height: 80px;
  margin-top: 30px;
  margin-bottom: 30px;
  background: transparent url(/web/static/img/empty_folder.svg) no-repeat center;
}

