
/* /im_livechat/static/src/legacy/public_livechat_chatbot.scss */

.o_thread_window .o_livechat_chatbot_main_restart {
  color: white;
  opacity: 0.7;
}

.o_thread_window .o_livechat_chatbot_main_restart:hover, .o_thread_window .o_livechat_chatbot_main_restart:focus, .o_thread_window .o_livechat_chatbot_main_restart.focus {
  opacity: 1;
}

@media (max-width: 767.98px) {
  .o_thread_window .o_livechat_chatbot_main_restart {
    padding: 14.5px;
    font-size: 17px;
    opacity: 1;
  }
}

.o_thread_window .o_livechat_chatbot_options {
  list-style: none;
  margin-block-start: 0px;
  margin-block-end: 0px;
  padding-inline-start: 0px;
}

.o_thread_window .o_livechat_chatbot_options li {
  user-select: none;
}

.o_thread_window .o_livechat_chatbot_options li:not(.disabled) {
  cursor: pointer;
}

.o_thread_window .o_livechat_chatbot_options li:not(.disabled):hover {
  color: white;
  background-color: #6772E5;
}

