.o_knowledge_behavior_type_file {
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #F9FAFB;
  outline: 1px solid #d8dadd;
  outline-offset: -1px;
  display: -webkit-box; display: -webkit-flex; display: flex;
  padding: 5px;
}

.o_knowledge_behavior_type_template {
  margin-bottom: 1rem;
  border-radius: 5px;
  background-color: #F9FAFB;
  outline: 1px solid #d8dadd;
  outline-offset: -1px;
}

.o_knowledge_behavior_type_template .o_knowledge_content {
  padding: 5px;
  border-bottom: 5px solid #F9FAFB;
  margin: 0 5px;
  background-color: white;
}

.o_knowledge_behavior_type_template .o_knowledge_content p:last-child {
  margin-bottom: 0;
}

.o_knowledge_behavior_type_template .o_knowledge_template_label {
  font-family: var(--font-sans-serif) !important;
  font-weight: 700;
  padding: 0.0625rem 0.3125rem;
  color: #5f636f;
}

.o_knowledge_behavior_type_toc {
  box-shadow: 0 2px 4px 4px #e7e9ed;
  padding: 10px;
  margin: 15px 0 15px 0;
}

.o_knowledge_behavior_type_toc > .o_knowledge_toc_content a.o_knowledge_toc_link_depth_0 {
  padding-left: calc(5px + Min(30px, 10%) * 0);
  font-family: var(--font-sans-serif) !important;
  font-weight: bolder !important;
  border-radius: 5px;
  min-height: 1.3125rem;
  border: transparent;
}

.o_knowledge_behavior_type_toc > .o_knowledge_toc_content a.o_knowledge_toc_link_depth_0:hover {
  background-color: rgba(27, 161, 228, 0.2);
  text-decoration: none;
}

