
/* /website_sale/static/src/scss/website_sale_frontend.scss */

.progress-wizard {
  margin-top: 15px;
  padding: 0 15px;
}

@media (min-width: 768px) {
  .progress-wizard {
    padding: 0;
  }
}

@media (max-width: 767.98px) {
  .progress-wizard .col {
    flex: none;
  }
}

.progress-wizard .progress-wizard-step {
  position: relative;
}

@media (min-width: 768px) {
  .progress-wizard .progress-wizard-step {
    margin-top: 0.8125rem;
  }
}

@media (max-width: 767.98px) {
  .progress-wizard .progress-wizard-step.disabled, .progress-wizard .progress-wizard-step.complete {
    display: none;
  }
}

.progress-wizard .progress-wizard-step .progress-wizard-dot {
  width: 0.625rem;
  height: 0.625rem;
  position: relative;
  display: inline-block;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 0 0.125rem #E9ECEF;
}

@media (min-width: 768px) {
  .progress-wizard .progress-wizard-step .progress-wizard-dot {
    position: absolute;
    top: auto;
    left: 50%;
    bottom: auto;
    right: auto;
    margin: -0.375rem 0 0 -0.3125rem;
  }
}

.progress-wizard .progress-wizard-step .progress-wizard-steplabel {
  color: rgba(33, 37, 41, 0.7);
  margin: 5px 0 5px 5px;
  font-size: 0.875rem;
  display: inline-block;
}

@media (min-width: 768px) {
  .progress-wizard .progress-wizard-step .progress-wizard-steplabel {
    display: block;
    margin: 1rem 0 20px 0;
  }
}

@media (max-width: 767.98px) {
  .progress-wizard .progress-wizard-step .progress-wizard-steplabel {
    margin-left: -15px;
    font-size: 24px;
  }
}

.progress-wizard .progress-wizard-step .progress-wizard-bar {
  height: 0.125rem;
  background-color: #E9ECEF;
}

.progress-wizard .progress-wizard-step.active .progress-wizard-dot {
  animation: fadeIn 1s ease 0s 1 normal none running;
  background: #6772E5;
  box-shadow: 0 0 0 0.1875rem white, 0 0 0 0.25rem rgba(103, 114, 229, 0.5);
}

.progress-wizard .progress-wizard-step.active .progress-wizard-steplabel {
  color: #212529;
  font-weight: bolder;
}

.progress-wizard .progress-wizard-step.complete .progress-wizard-dot {
  background: none;
  box-shadow: none;
}

.progress-wizard .progress-wizard-step.complete .progress-wizard-dot:after {
  position: absolute;
  top: -0.3125rem;
  left: -0.3125rem;
  bottom: auto;
  right: auto;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 100%;
  background: white;
  color: #28a745;
  text-align: center;
  line-height: 1;
  font-size: 1.25rem;
  font-family: FontAwesome;
  content: "\f058";
}

.progress-wizard .progress-wizard-step.complete .progress-wizard-steplabel {
  color: #28a745;
}

.progress-wizard .progress-wizard-step.complete:hover:not(.disabled) .progress-wizard-dot:after {
  color: #28a745;
}

.progress-wizard .progress-wizard-step.complete:hover:not(.disabled) .progress-wizard-steplabel {
  color: #212529;
}

.progress-wizard .progress-wizard-step.disabled {
  cursor: default;
}

.o_wsale_image_viewer {
  z-index: -1;
}

.o_wsale_image_viewer .o_wsale_image_viewer_header {
  height: 40px;
  z-index: 1;
}

.o_wsale_image_viewer .o_wsale_image_viewer_image .o_wsale_image_viewer_void {
  padding-top: 64px;
  padding-bottom: 156px;
}

.o_wsale_image_viewer .o_wsale_image_viewer_image img {
  cursor: zoom-in;
}

.o_wsale_image_viewer .o_wsale_image_viewer_carousel {
  z-index: 1;
}

.o_wsale_image_viewer .o_wsale_image_viewer_carousel ol {
  list-style: none;
}

.o_wsale_image_viewer .o_wsale_image_viewer_carousel ol .o_wsale_image_viewer_thumbnail {
  width: 128px;
  height: 128px;
}

.o_wsale_image_viewer .o_wsale_image_viewer_carousel ol li img {
  border: 1px solid #CED4DA;
}

.o_wsale_image_viewer .o_wsale_image_viewer_carousel ol li img.active {
  border-color: #35979c;
}

.o_wsale_image_viewer .o_wsale_image_viewer_control {
  z-index: 1;
  width: 40px;
  height: 40px;
}

.o_wsale_image_viewer .o_wsale_image_viewer_control:hover {
  background-color: rgba(206, 212, 218, 0.1);
}

.o_wsale_image_viewer .o_wsale_image_viewer_control.o_wsale_image_viewer_previous {
  margin: 1px 1px 0 0;
}

.o_wsale_image_viewer .o_wsale_image_viewer_control.o_wsale_image_viewer_next {
  margin: 1px 0 0 1px;
}

