
/* /web/static/src/core/popover/popover.scss */

/*!rtl:begin:ignore*/
@keyframes slide-top {
  0% {
    opacity: 0;
    transform: translateY(-5%);
  }
}

@keyframes slide-end {
  0% {
    opacity: 0;
    transform: translateX(5%);
  }
}

@keyframes slide-bottom {
  0% {
    opacity: 0;
    transform: translateY(5%);
  }
}

@keyframes slide-start {
  0% {
    opacity: 0;
    transform: translateX(-5%);
  }
}

.o_popover.bs-popover-top, .o_popover.bs-popover-auto[data-popper-placement^="top"] {
  animation: 0.2s slide-top;
}

.o_popover.bs-popover-end, .o_popover.bs-popover-auto[data-popper-placement^="right"] {
  animation: 0.2s slide-end;
}

.o_popover.bs-popover-bottom, .o_popover.bs-popover-auto[data-popper-placement^="bottom"] {
  animation: 0.2s slide-bottom;
}

.o_popover.bs-popover-start, .o_popover.bs-popover-auto[data-popper-placement^="left"] {
  animation: 0.2s slide-start;
}

.o_popover.o-popover-top, .o_popover.o-popover-auto[x-placement^="top"] {
  margin-bottom: 0.5rem;
}

.o_popover.o-popover-right, .o_popover.o-popover-auto[x-placement^="right"] {
  margin-left: 0.5rem;
}

.o_popover.o-popover-bottom, .o_popover.o-popover-auto[x-placement^="bottom"] {
  margin-top: 0.5rem;
}

.o_popover.o-popover-left, .o_popover.o-popover-auto[x-placement^="left"] {
  margin-right: 0.5rem;
}

.o_popover.o-popover--ts .popover-arrow, .o_popover.o-popover--bs .popover-arrow {
  left: 0.3rem;
}

.o_popover.o-popover--te .popover-arrow, .o_popover.o-popover--be .popover-arrow {
  right: 0.3rem;
}

.o_popover.o-popover--ls .popover-arrow, .o_popover.o-popover--rs .popover-arrow {
  top: 0.3rem;
}

.o_popover.o-popover--le .popover-arrow, .o_popover.o-popover--re .popover-arrow {
  bottom: 0.3rem;
}

/*!rtl:end:ignore*/
