
/* /payment/static/src/scss/payment_form.scss */

.o_payment_form label > input[type="radio"], .o_payment_form input[type="checkbox"] {
  vertical-align: middle;
  margin-right: 5px;
}

.o_payment_form .payment_option_name {
  font-size: 14px;
  font-weight: normal !important;
  font-family: Helvetica Neue, sans-serif;
  line-height: 1.3em;
  color: #4d4d4d;
}

.o_payment_form label {
  font-weight: normal;
  margin-top: 5px;
}

.o_payment_form .card {
  border-radius: 5px;
  overflow: hidden;
}

.o_payment_form .card-body {
  padding: 1.14em !important;
}

.o_payment_form .card-body:not(:first-child) {
  border-top: 1px solid #dddddd;
}

.o_payment_form .card-body.o_payment_option_card:hover {
  cursor: pointer;
}

.o_payment_form .card-footer {
  padding: 0.5rem;
}

.o_payment_form .card-footer label {
  margin-top: 15px;
}

.o_payment_form .card-footer:last-child {
  border-bottom-right-radius: 10px !important;
  border-bottom-left-radius: 10px !important;
}

.o_payment_form .payment_icon_list {
  position: relative;
  margin-top: 0px !important;
  margin-bottom: -5px !important;
}

.o_payment_form .payment_icon_list li {
  padding-left: 5px !important;
  padding-right: 0px !important;
}

.o_payment_form .payment_icon_list .more_option {
  position: absolute;
  top: auto;
  left: auto;
  bottom: auto;
  right: 10px;
  font-size: 10px;
}

