html, body {
  position: relative;
  width: 100%;
  height: 100%;
}

*[contenteditable=true] {
  outline: none;
}

[contenteditable] {
  overflow-wrap: unset !important;
}

.css_non_editable_mode_hidden {
  display: none !important;
}

.editor_enable .css_editable_mode_hidden {
  display: none !important;
}

.note-toolbar {
  margin-left: 0 !important;
}

.note-popover .popover > .arrow {
  display: none;
}

.note-popover .popover .dropdown-menu .dropdown-item > i, .note-popover .popover .o_dropdown_menu .dropdown-item > i, .note-editor .dropdown-menu .dropdown-item > i, .note-editor .o_dropdown_menu .dropdown-item > i {
  visibility: hidden;
}

.note-popover .popover .dropdown-menu .dropdown-item.checked > i, .note-popover .popover .o_dropdown_menu .dropdown-item.checked > i, .note-editor .dropdown-menu .dropdown-item.checked > i, .note-editor .o_dropdown_menu .dropdown-item.checked > i {
  visibility: visible;
}

/* ----- GENERIC LAYOUTING HELPERS ---- */
/* table */
#wrapwrap table.table.table-bordered, .o_editable table.table.table-bordered {
  table-layout: fixed;
}

#wrapwrap table.table.table-bordered td, .o_editable table.table.table-bordered td {
  min-width: 20px;
}

@media (max-width: 767.98px) {
  #wrapwrap .table-responsive > table.table, .o_editable .table-responsive > table.table {
    table-layout: auto;
  }
}

ul.o_checklist {
  list-style: none;
}

ul.o_checklist > li {
  list-style: none;
  position: relative;
  margin-left: 20px;
  margin-right: 20px;
}

ul.o_checklist > li:not(.oe-nested)::before {
  content: '';
  position: absolute;
  left: -20px;
  display: block;
  height: 13px;
  width: 13px;
  top: 4px;
  border: 1px solid;
  text-align: center;
  cursor: pointer;
}

ul.o_checklist > li.o_checked {
  text-decoration: line-through;
}

ul.o_checklist > li.o_checked::before {
  content: "✓";
  display: -webkit-box; display: -webkit-flex; display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 1px /*rtl:ignore*/;
  padding-top: 1px;
}

ul.o_checklist[dir="rtl"] > li:not(.oe-nested)::before {
  left: auto;
  right: -20px;
  text-align: right;
}

ol > li.o_indent, ul > li.o_indent {
  margin-left: 0;
  list-style: none;
}

ol > li.o_indent::before, ul > li.o_indent::before {
  content: none;
}

.o_stars .fa.fa-star {
  color: gold;
}

img.o_we_custom_image {
  display: inline-block;
}

img.shadow {
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
}

img.padding-small, .img.padding-small, span.fa.padding-small, iframe.padding-small {
  padding: 4px;
}

img.padding-medium, .img.padding-medium, span.fa.padding-medium, iframe.padding-medium {
  padding: 8px;
}

img.padding-large, .img.padding-large, span.fa.padding-large, iframe.padding-large {
  padding: 16px;
}

img.padding-xl, .img.padding-xl, span.fa.padding-xl, iframe.padding-xl {
  padding: 32px;
}

img.ms-auto, img.mx-auto, img.o_portal_security_body {
  display: block;
}

.fa-6x {
  font-size: 6em;
}

.fa-7x {
  font-size: 7em;
}

.fa-8x {
  font-size: 8em;
}

.fa-9x {
