}

ul.oe_menu_editor .oe_menu_placeholder {
  outline: 1px dashed #4183C4;
}

ul.oe_menu_editor ul {
  list-style: none;
}

ul.oe_menu_editor li div {
  cursor: url(/web/static/img/openhand.cur), grab;
}

ul.oe_menu_editor li div :active {
  cursor: grabbing;
