.o_ace_view_editor {
  background: #2F3129;
  color: white;
  display: -webkit-box; display: -webkit-flex; display: flex;
  -webkit-flex-flow: column nowrap; flex-flow: column nowrap;
  opacity: 0.97;
}

.o_ace_view_editor .o_ace_view_editor_title {
  -webkit-box-flex: 0; -webkit-flex: 0 0 auto; flex: 0 0 auto;
  display: -webkit-box; display: -webkit-flex; display: flex;
  align-items: center;
  padding: 7.5px;
}

.o_ace_view_editor .o_ace_view_editor_title > .o_ace_type_switcher > button::after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  vertical-align: middle;
  -moz-transform: scale(0.9999);
  border-bottom: 0;
  border-left: 0.3em solid transparent;
  border-right: 0.3em solid transparent;
  border-top: 0.3em solid var(--o-caret-color, currentColor);
  margin-left: 4px;
}

.o_ace_view_editor .o_ace_view_editor_title > * {
  -webkit-box-flex: 0; -webkit-flex: 0 0 auto; flex: 0 0 auto;
  margin: 0 7.5px;
}

.o_ace_view_editor .o_ace_view_editor_title > *.o_include_option {
  display: -webkit-box; display: -webkit-flex; display: flex;
  align-items: center;
  font-size: 11px;
}

.o_ace_view_editor .o_ace_view_editor_title > *.o_include_option > .form-check {
  margin-right: 7.5px;
}

.o_ace_view_editor .o_ace_view_editor_title > *.o_res_list {
  -webkit-box-flex: 1; -webkit-flex: 1 1 auto; flex: 1 1 auto;
  min-width: 60px;
}

.o_ace_view_editor #ace-view-id {
  -webkit-box-flex: 0; -webkit-flex: 0 0 auto; flex: 0 0 auto;
  padding: 7.5px 15px;
  background-color: #4a4d40;
}

.o_ace_view_editor #ace-view-id .o_ace_editor_resource_info {
  color: #ebecee;
}

.o_ace_view_editor #ace-view-editor {
  height: 70%;
  -webkit-box-flex: 1; -webkit-flex: 1 1 auto; flex: 1 1 auto;
}

.o_ace_view_editor #ace-view-editor .ace_gutter {
  cursor: ew-resize;
}

.o_ace_view_editor #ace-view-editor .ace_gutter .ace_gutter-cell.o_error {
  position: relative;
}

.o_ace_view_editor #ace-view-editor .ace_gutter .ace_gutter-cell.o_error::before {
  position: absolute;
  top: -100vh;
  left: auto;
  bottom: -100vh;
  right: 0;
  content: "";
  z-index: 1000;
  display: -webkit-box; display: -webkit-flex; display: flex;
  background-color: #e6586c;
  color: white;
  font-size: 1.2em;
  align-items: center;
  justify-content: center;
  cursor: help;
  width: 2px;
}

.o_ace_view_editor #ace-view-editor .ace_gutter .ace_gutter-cell.o_error::after {
  position: absolute;
  top: -100%;
  left: 0;
  bottom: -100%;
  right: 0;
  content: "";
  z-index: 1000;
  display: -webkit-box; display: -webkit-flex; display: flex;
  background-color: #e6586c;
  color: white;
  font-size: 1.2em;
  align-items: center;
  justify-content: center;
  cursor: help;
  font-family: FontAwesome;
  content: "\f071";
}

.o_ace_view_editor #ace-view-editor .ace_resize_bar {
  position: absolute;
  top: auto;
  left: auto;
  bottom: auto;
  right: 0;
  width: 25px;
  height: 100%;
  cursor: ew-resize;
}

.o_ace_view_editor_resizing iframe {
  pointer-events: none;
}

.o_ace_error_popover {
  max-width: 40vw;
}

.o_ace_error_popover .popover-body {
  background-color: #4a4d40;
  color: #ebecee;
}

.o_ace_error_popover.bs-popover-left .arrow::after {
