
/* /website_livechat/static/src/legacy/public_livechat.scss */

.o_website_livechat_button {
  display: -webkit-box; display: -webkit-flex; display: flex;
  justify-content: center;
  align-items: center;
  transition: filter 0.3s;
  padding: 0px;
  margin: 0 30px 30px 0;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  border: none;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  min-width: 56px;
  font-size: 24px;
  font-weight: normal;
  text-shadow: none;
}

.o_website_livechat_button.o-isExternalLib::before {
  content: "💬";
}

.o_website_livechat_button:hover {
  filter: brightness(90%);
}

