
/* /website_sale_comparison/static/src/scss/website_sale_comparison.scss */

.o_product_feature_panel {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  border: 2px solid;
}

.o_product_feature_panel .o_product_panel {
  position: relative;
}

.o_product_feature_panel .o_product_panel .o_product_panel_header {
  margin: 0 10px 0 10px;
  cursor: pointer;
}

.o_product_feature_panel .o_product_panel .o_product_panel_header .o_product_icon {
  margin-right: 5px;
}

.o_product_feature_panel .o_product_panel .o_product_panel_header .o_product_text {
  text-transform: uppercase;
  vertical-align: middle;
  font-size: 16px;
}

.o_product_feature_panel .o_product_panel .o_product_panel_header .o_product_circle {
  vertical-align: 6px;
  padding: 0 3px;
  line-height: 14px;
}

.o_product_feature_panel .o_product_panel .o_product_panel_content {
  display: none !important;
}

.oe_website_sale .product_summary > * {
  display: block;
  margin: 15px 0 15px 0;
}

.oe_website_sale .table-comparator .o_product_comparison_collpase {
  margin-right: 8px;
}

.oe_website_sale div.css_not_available .o_add_compare_dyn {
  display: none;
}

.oe_website_sale .o_comparelist_remove {
  position: absolute;
  top: 0;
  left: auto;
  bottom: auto;
  right: 0.5rem;
}

.oe_website_sale .o_ws_compare_image {
  vertical-align: middle;
}

#product_full_spec {
  border-top: 1px solid #CED4DA;
}

#product_full_spec .o_add_compare_dyn {
  font-size: 1.1rem;
}

