
/* /web_editor/static/src/components/media_dialog/media_dialog.scss */

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments {
  min-height: 128px;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_we_attachment_placeholder {
  flex-grow: 128;
  flex-basis: 128px;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_cell.o_we_image {
  transition: opacity 0.5s ease 0.5s;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_remove {
  border-radius: 0 0 0 4px;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_existing_attachments .o_existing_attachment_remove:hover {
  color: #e6586c;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_attachment_selected {
  box-shadow: 0 0 0 3px #714B67;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_attachment_selected:not(.fa) {
  border: 3px solid #714B67;
  box-shadow: none;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_attachment_selected:not(.fa):before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  bottom: auto;
  right: auto;
  width: 19px;
  height: 19px;
  background-color: #714B67;
  font-family: 'FontAwesome';
  color: white;
  border-radius: 50%;
  text-align: center;
  z-index: 1;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_we_load_more {
  scroll-margin: 1rem;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .font-icons-icons > span {
  width: 50px;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_video_dialog_form textarea {
  min-height: 95px;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_video_preview {
  border-top: 1px solid black;
  border-bottom: 1px solid white;
  background-image: linear-gradient(-150deg, #2b2b33, #191922);
  color: white;
}

.modal:not(.o_legacy_dialog) .o_select_media_dialog .o_video_preview .media_iframe_video {
  width: 100%;
}

