
/* /web/static/src/scss/bootstrap_review.scss */

.alert {
  clear: both;
}

.card-body {
  background-color: rgba(255, 255, 255, 0.9) !important;
  color: #212529;
}

.card-body .text-muted, .o_colored_level .card-body .text-muted {
  color: rgba(33, 37, 41, 0.7) !important;
}

.card-body:first-child {
  border-top-left-radius: calc(0.25rem - 1px);
  border-top-right-radius: calc(0.25rem - 1px);
}

.card-body:last-child {
  border-bottom-right-radius: calc(0.25rem - 1px);
  border-bottom-left-radius: calc(0.25rem - 1px);
}

.row.card-body {
  background-color: transparent !important;
}

.accordion .collapsing > .card-body:first-child, .accordion .collapse.show > .card-body:first-child {
  margin-top: 1px;
}

.toast-header {
  background-clip: border-box;
}

.toast-body {
  background-color: rgba(255, 255, 255, 0.93) !important;
  color: #212529;
}

.toast-body .text-muted, .o_colored_level .toast-body .text-muted {
  color: rgba(33, 37, 41, 0.7) !important;
}

@media (min-width: 576px) {
  :not(.s_popup) > .modal .modal-dialog {
    height: 100%;
    padding: 1.75rem 0;
    margin: 0 auto;
  }
  :not(.s_popup) > .modal .modal-content {
    max-height: 100%;
  }
  :not(.s_popup) > .modal .modal-header, :not(.s_popup) > .modal .modal-footer {
    -webkit-box-flex: 0; -webkit-flex: 0 0 auto; flex: 0 0 auto;
  }
  :not(.s_popup) > .modal .modal-body {
    overflow: auto;
    min-height: 0;
  }
}

.modal-backdrop {
  display: none;
}

.modal:not([data-bs-backdrop="false"]) {
  background-color: rgba(0, 0, 0, 0.5);
}

.form-check .form-check-input:not(:disabled):not(.o_wysiwyg_loader), .form-check .form-check-input:not(:disabled):not(.o_wysiwyg_loader) + label, .form-select .form-check-input:not(:disabled):not(.o_wysiwyg_loader), .form-select .form-check-input:not(:disabled):not(.o_wysiwyg_loader) + label {
  cursor: pointer;
}

.form-check:hover, .form-check:hover .form-check-input:not(:disabled):not(.o_wysiwyg_loader), .form-select:hover, .form-select:hover .form-check-input:not(:disabled):not(.o_wysiwyg_loader) {
