}

.dropdown-menu[x-placement^="top"], .o_dropdown_menu[x-placement^="top"], .dropdown-menu[x-placement^="right"], .o_dropdown_menu[x-placement^="right"], .dropdown-menu[x-placement^="bottom"], .o_dropdown_menu[x-placement^="bottom"], .dropdown-menu[x-placement^="left"], .o_dropdown_menu[x-placement^="left"] {
  /*rtl:ignore*/
  right: auto;
}

.popover {
  right: auto/*rtl:ignore*/;
}

