
/* /web/static/src/core/select_menu/select_menu.scss */

.o_select_menu .o_select_menu_toggler {
  display: grid;
  grid-template-columns: auto 25px;
}

.o_select_menu .o_select_menu_toggler.o_can_deselect {
  grid-template-columns: auto 25px 25px;
}

.o_select_menu .o_select_menu_toggler_slot {
  flex-grow: 2;
}

.o_select_menu .o_select_menu_toggler_caret {
  grid-column: 2;
}

.o_select_menu .o_can_deselect .o_select_menu_toggler_caret {
  grid-column: 3;
}

.o_select_menu .o_select_menu_toggler_clear {
  grid-column: 2;
}

.o_select_menu .o_select_menu_toggler_clear:hover i {
  color: red;
}

.o_select_menu .o_select_menu_menu {
  min-width: fit-content;
  max-height: 350px;
  border: 1px solid #714B67;
}

.o_select_menu .o_select_menu_menu .o_select_menu_sticky {
  background-color: #FFF !important;
}

.o_select_menu .o_select_menu_menu .o_select_menu_sticky.o_select_menu_item:focus, .o_select_menu .o_select_menu_menu .o_select_menu_sticky.o_select_menu_item:hover {
  background: rgba(0, 0, 0, 0.15) !important;
}

.o_select_menu .o_select_menu_menu .o_select_menu_group {
  top: 40px;
}

.o_select_menu .o_select_menu_multi_select .o_select_active {
  background: #714B67;
  background: linear-gradient(to right, #dc3545 50%, #714B67 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  transition: background 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}

.o_select_menu .o_select_menu_multi_select .o_select_active:hover {
  background-position: left bottom;
}

.o_select_menu .o_tag {
  margin: 2px;
}

.dropup .o_select_menu_menu {
  box-shadow: 0 -7px 10px rgba(8, 8, 8, 0.319);
  border-bottom: none;
}

.dropdown .o_select_menu_menu {
  box-shadow: 0 7px 10px rgba(8, 8, 8, 0.319);
  border-top: none;
}

