
/* /website/static/src/snippets/s_dynamic_snippet_carousel/000.scss */

.s_dynamic .carousel-control-prev, .s_dynamic .carousel-control-next {
  position: absolute;
  width: 4rem;
}

.s_dynamic .carousel-control-prev > span.fa, .s_dynamic .carousel-control-next > span.fa {
  color: #495057;
  background: radial-gradient(#FFF 50%, transparent 50%);
}

.s_dynamic .dynamic_snippet_template .card-footer a, .s_dynamic .dynamic_snippet_template .card-footer button, .s_dynamic .o_dynamic_snippet_btn_wrapper {
  z-index: 2;
}

.s_dynamic .dynamic_snippet_template .card-body .card-text {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

.s_dynamic .s_dynamic_snippet_arrow_bottom > .carousel-control-prev, .s_dynamic .s_dynamic_snippet_arrow_bottom .carousel-control-next {
  position: relative;
}

