
/* /knowledge/static/src/scss/knowledge_frontend.scss */

.o_knowledge_form_view .o_knowledge_header {
  cursor: default;
}

.o_knowledge_form_view h1, .o_knowledge_form_view .h1, .o_knowledge_form_view h2, .o_knowledge_form_view .h2, .o_knowledge_form_view h3, .o_knowledge_form_view .h3, .o_knowledge_form_view h4, .o_knowledge_form_view .h4, .o_knowledge_form_view h5, .o_knowledge_form_view .h5, .o_knowledge_form_view h6, .o_knowledge_form_view .h6 {
  transition: background-color 0.5s ease;
}

.o_knowledge_form_view h1.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h1, .o_knowledge_form_view h2.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h2, .o_knowledge_form_view h3.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h3, .o_knowledge_form_view h4.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h4, .o_knowledge_form_view h5.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h5, .o_knowledge_form_view h6.o_knowledge_header_highlight, .o_knowledge_form_view .o_knowledge_header_highlight.h6 {
  border-radius: 0.25rem;
  background-color: rgba(27, 161, 228, 0.2);
}

.o_knowledge_form_view .o_knowledge_icon:not(.o_full_width),
.o_knowledge_form_view .o_knowledge_article:not(.o_full_width) {
  width: 750px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.o_knowledge_form_view .o_knowledge_cover + .o_knowledge_icon {
  margin-top: -70px;
}

.o_knowledge_form_view .o_knowledge_icon {
  font-size: 80px;
}

.o_knowledge_form_view .o_knowledge_aside {
  font-size: 1rem;
}

.o_knowledge_form_view .o_knowledge_aside .o_section_header {
  font-size: 1.3125rem;
}

.o_knowledge_form_view .o_knowledge_aside .knowledge_search_bar {
  font-size: 0.875rem;
}

.o_knowledge_form_view .o_knowledge_aside .o_article_active > div {
  background-color: rgba(27, 161, 228, 0.2);
}

.o_knowledge_form_view .o_knowledge_aside .o_article_active > div:hover {
  background-color: rgba(27, 161, 228, 0.3);
}

.o_knowledge_form_view .o_knowledge_aside .o_article_name a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.o_knowledge_form_view .o_knowledge_embedded_view_placeholder .o_knowledge_embedded_view_placeholder_cover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: radial-gradient(#FFF, transparent);
}

@media (max-width: 575.98px) {
  .o_knowledge_form_view .o_knowledge_aside {
    width: 100%;
  }
}

@media (min-width: 576px) {
  .o_knowledge_form_view .o_scroll_view {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow-y: auto;
    overflow-x: hidden;
  }
}

@media (max-width: 575.98px) {
  .o_knowledge_portal_virality {
    display: none;
  }
}

@media (min-width: 576px) and (max-width: 1199.98px) {
  .o_knowledge_portal_virality {
    font-size: .85rem;
  }
}

@media (min-width: 1200px) {
  .o_knowledge_portal_virality {
    font-size: .9rem;
  }
}

.o_knowledge_public_virality {
  table-layout: fixed;
}

@media (max-width: 575.98px) {
  .o_knowledge_public_virality:has(#knowledge_virality_try_odoo) {
    width: 100%;
  }
}

