
/* /website/static/src/snippets/s_process_steps/001.scss */

.s_process_steps[data-vcss='001'] .s_process_step {
  position: relative;
}

.s_process_steps[data-vcss='001'] .s_process_step .s_process_step_icon {
  position: relative;
  margin: 30px 0;
}

.s_process_steps[data-vcss='001'] .s_process_step .s_process_step_icon .fa {
  display: block;
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
}

.s_process_steps[data-vcss='001'] .s_process_step .s_process_step_content {
  padding: 0 15px;
  text-align: center;
}

.s_process_steps[data-vcss='001'] .s_process_step .s_process_step_connector {
  position: absolute;
  z-index: 1;
  height: 5rem;
  width: calc(100% - 5rem);
  left: calc(50% + 2.5rem);
  margin: 30px 0;
}

.s_process_steps[data-vcss='001'] .s_process_step .s_process_step_connector path {
  stroke: #6C757D;
  stroke-width: 2;
  fill: transparent;
}

.s_process_steps[data-vcss='001'] .s_process_step:last-child .s_process_step_connector {
  display: none;
}

.s_process_steps[data-vcss='001'].s_process_steps_connector_curved_arrow .s_process_step:nth-child(odd) .s_process_step_connector {
  transform: scale(1, -1);
}

.s_process_steps[data-vcss='001'] .s_process_steps_arrow_head path {
  fill: #6C757D;
  stroke: transparent;
}

@media (max-width: 991.98px) {
  .s_process_steps[data-vcss='001'] .s_process_step_connector {
    display: none;
  }
}

