
/* /web/static/src/core/datetime/datetime_picker.scss */

.o_datetime_picker {
  --DateTimePicker__Cell-size-md: 3rem;
  --DateTimePicker__Cell-size-lg: 5rem;
  --DateTimePicker__Template-rows: 3;
  --DateTimePicker__Template-columns: 4;
  --DateTimePicker__Day-template-rows: 6;
}

.o_datetime_picker .o_datetime_picker_header .o_header_part {
  text-transform: none;
}

.o_datetime_picker .o_date_item_cell {
  position: relative;
  border-radius: 0;
}

.o_datetime_picker .o_current,
.o_datetime_picker .o_selected {
  color: #111827;
}

.o_datetime_picker .o_selected:not(.o_select_start):not(.o_select_end) {
  background: #e6f2f3;
}

.o_datetime_picker .o_current:before,
.o_datetime_picker .o_highlight_start:not(.o_selected):before,
.o_datetime_picker .o_highlight_end:not(.o_selected):before,
.o_datetime_picker .o_select_start:before,
.o_datetime_picker .o_select_end:before {
  content: "";
  position: absolute;
  box-shadow: inset 0 0 0 1px #017e84;
  width: 100%;
  aspect-ratio: 1;
  border-radius: 100%;
  z-index: 1;
}

.o_datetime_picker .o_select_start:before,
.o_datetime_picker .o_select_end:before {
  background: #c4e8ea;
}

.o_datetime_picker .o_select_start:after,
.o_datetime_picker .o_select_end:after {
  content: "";
  position: absolute;
  background: transparent;
  width: 50%;
  aspect-ratio: 1/2;
}

.o_datetime_picker .o_select_start:not(.o_select_end):after,
.o_datetime_picker .o_select_end:not(.o_select_end):after {
  right: 0;
  background: #e6f2f3;
}

.o_datetime_picker .o_select_start:not(.o_select_start):after,
.o_datetime_picker .o_select_end:not(.o_select_start):after {
  right: 50%;
  background: #e6f2f3;
}

.o_datetime_picker .o_today:not(.o_out_of_range) span {
  position: relative;
}

.o_datetime_picker .o_today:not(.o_out_of_range) span::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -0.25rem;
  transform: translateX(-50%);
  width: 0.95em;
  height: 0.2em;
  border-radius: 50rem;
  background: #dc3545;
}

.o_datetime_picker .o_out_of_range {
  color: var(--gray-400);
}

.o_datetime_picker .o_time_picker_select {
  background: none;
}

.o_datetime_picker .o_time_picker_select:focus, .o_datetime_picker .o_time_picker_select:hover {
  border-color: var(--primary);
}

.o_datetime_picker .o_date_picker {
  grid-template-rows: repeat(var(--DateTimePicker__Day-template-rows), 1fr);
  grid-template-columns: repeat(var(--DateTimePicker__Day-template-columns), 1fr);
}

.o_datetime_picker .o_date_item_picker {
  grid-template-rows: repeat(var(--DateTimePicker__Template-rows), 1fr);
  grid-template-columns: repeat(var(--DateTimePicker__Template-columns), 1fr);
}

.o_datetime_picker .o_date_item_picker .o_datetime_button.o_selected:not(.o_select_start):not(.o_select_end), .o_datetime_picker .o_date_item_picker .o_datetime_button:hover:not(.o_select_start):not(.o_select_end), .o_datetime_picker .o_date_item_picker .o_datetime_button.o_today:not(.o_selected):hover:not(.o_select_start):not(.o_select_end) {
  background: #e6f2f3;
  color: #111827;
}

.o_datetime_picker .o_center {
  display: grid;
  place-items: center;
}

.o_datetime_picker .o_zoom_out {
  gap: 5rem;
}

.o_datetime_picker .o_cell_md {
  padding: 0.4rem;
  width: var(--DateTimePicker__Cell-size-md);
  height: var(--DateTimePicker__Cell-size-md);
}

.o_datetime_picker .o_cell_lg {
  width: var(--DateTimePicker__Cell-size-lg);
  height: var(--DateTimePicker__Cell-size-lg);
}

.o_datetime_picker .o_text_sm {
  font-size: 0.875rem;
}

.o_datetime_picker .o_time_picker {
  /*rtl:ignore*/
  direction: ltr;
}

