
/* /web/static/src/core/notifications/notification.scss */

.o_notification_manager {
  position: absolute;
  top: 52.9px;
  left: calc(100vw - 320px);
  bottom: auto;
  right: 0.5rem;
  z-index: 1055;
}

.o_notification_manager .o_notification {
  border-left-width: 0.75rem !important;
  box-shadow: 0 12px 14px -10px rgba(0, 0, 0, 0.25);
}

.o_notification_manager .o_notification_close {
  position: absolute;
  top: 0;
  left: auto;
  bottom: auto;
  right: 0;
}

.o_notification_fade {
  transition: all 0.5s;
}

.o_notification_fade-enter {
  opacity: 0;
}

