
/* /appointment/static/src/scss/appointment.scss */

.o_appointment_index {
  min-height: 80vh;
}

.o_appointment_index .o_half_screen_height, .o_appointment_index .cover_mid {
  min-height: 200px !important;
}

.o_appointment_index .o_appointment_appointments_list header a {
  transition: all .25s ease-in-out;
}

.o_appointment_index .o_appointment_appointments_list header a:hover, .o_appointment_index .o_appointment_appointments_list header a:focus {
  transform: scale(1.05);
}

.o_appointment_index .o_appointment_appointments_list .o_appointment_unpublished {
  bottom: 0;
  padding: 1rem 1rem;
}

.o_appointment_index .o_appointment_appointments_list_form #appointment_type_id {
  height: 2.5rem;
}

.o_appointment_index .o_appointment_appointments_list_form #appointment_type_id:disabled, .o_appointment_index .o_appointment_appointments_list_form #appointment_type_id.o_wysiwyg_loader {
  background-color: #FAFAFA;
}

.o_appointment_index .o_appointment_svg {
  max-width: 500px;
}

@media screen and (min-device-width: 769px) {
  .o_appointment_slots_loading {
    min-height: 35em;
  }
}

.modal-open > .modal-backdrop {
  z-index: 999;
}

.o_appointment_days {
  font-size: 12px;
  height: 64px;
}

.o_appointment_days .o_weekend {
  background-color: #d1d5f7;
  color: #212529;
}

.o_appointment_days .o_day_wrapper {
  min-width: 54px;
}

@media (max-width: 575.98px) {
  .o_appointment_days .o_day_wrapper {
    min-width: 28px;
  }
}

.o_appointment_days .o_day .o_slot_button {
  border: 1px solid #6772E5;
  border-radius: 0.5rem;
}

.o_appointment_days .o_slot_button:hover, .o_appointment_days .o_slot_selected {
  color: #FFFFFF;
  background-color: #6772E5;
}

.o_appointment_days .o_mute_day {
  opacity: 0.5;
}

.o_appointment_days .o_today .o_day_wrapper:after {
  content: "";
  display: block;
  border-bottom: 2px solid #F5F9F9 !important;
}

.o_appointment_month .table {
  border-collapse: separate;
  border-spacing: unset;
}

.o_appointment_month .table th, .o_appointment_month .table td {
  width: 3em;
}

.o_appointment .o_appointment_selected_slot_date {
  font-size: 18px;
  font-weight: 500;
}

@media screen and (min-device-width: 769px) {
  .o_appointment .o_appointment_details_column {
    min-height: 45em;
  }
}

.o_appointment .o_appointment_fa_width {
  width: 1.5rem !important;
}

.o_appointment .o_appointment_no_slot_month_helper > * {
  width: 75%;
}

@media screen and (max-width: 400px) {
  .o_appointment .o_appointment_no_slot_month_helper > * {
    width: 95%;
  }
}

.o_appointment .o_appointment_no_slot_month_helper .o_appointment_no_slot_month_helper_svg {
  max-width: 400px;
}

.o_appointment .o_appointment_no_slot_overall_helper_svg {
  max-width: 450px;
}

.o_appointment .o_appointment_availabilities .o_slots_list a, .o_appointment .o_appointment_availabilities .o_slots_list button {
  color: #212529;
  height: 50px;
  border-color: #d1d5f7;
  font-weight: 500;
  font-size: 16px;
}

.o_appointment .o_appointment_availabilities .o_slots_list a.o_slot_hours_selected, .o_appointment .o_appointment_availabilities .o_slots_list a:hover, .o_appointment .o_appointment_availabilities .o_slots_list button.o_slot_hours_selected, .o_appointment .o_appointment_availabilities .o_slots_list button:hover {
  color: #FFFFFF !important;
  background-color: #6772E5;
}

.o_appointment .o_appointment_attendee_form input {
  background-color: #ffffff;
  color: #212529;
}

.o_appointment .o_appointment_attendee_form input::placeholder {
  color: darkgray;
}

.o_appointment .o_appointment_attendee_form .o_appointment_attendee_form_details {
  padding-left: calc(1px + 1.25rem);
}

.o_appointment .o_appointment_user_short_card header {
  height: 300px;
}

.o_appointment .o_appointment_user_short_card .card-body {
  padding: 0.75rem 1.25rem;
}

.o_appointment .o_appointment_validation_details label {
  font-size: 18px;
  font-weight: 500;
}

.o_appointment .o_appointment_validation_details .row {
  padding-bottom: 0.75rem;
}

.o_appointment .o_appointment_validation_details .o_pt_2_px {
  padding-top: 2px;
}

.o_appointment .o_appointment_validation_bottom_btns {
  border-top: 1px solid #DEE2E6;
}

.o_appointment .o_appointment_validation_bottom_btns .o_outlook_calendar {
  background-color: #1976D2;
  color: white;
}

.o_appointment .o_appointment_validation_bottom_btns .o_outlook_calendar:hover {
  background-color: #144c85;
}

.o_appointment .o_appointment_validation_bottom_btns .o_google_calendar {
  background-color: white;
  font-weight: 500;
  color: black;
  border: 1px solid #DEE2E6;
}

.o_appointment .o_appointment_validation_bottom_btns .o_google_calendar:hover {
  background-color: #E9ECEF;
}

.o_appointment .o_appointment_validation_bottom_btns a {
  border-radius: 5px;
}

.o_appointment .o_appointment_validation_bottom_btns a img {
  padding-right: 6px;
}

@media screen and (max-width: 768px) {
  .o_appointment .o_appointment_validation_bottom_btns {
    text-align: right;
  }
}

.o_appointment .alert-info {
  background: #CFF4FC;
  border: 1px solid #9EEAF9;
  color: #055160;
  padding: 1rem;
}

.o_appointment .o_appointment_avatar_container, .o_appointment_choice .o_appointment_avatar_container {
  position: relative;
}

.o_appointment .o_appointment_avatar_container .o_appointment_avatar_background, .o_appointment_choice .o_appointment_avatar_container .o_appointment_avatar_background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.o_appointment .o_appointment_avatar_background, .o_appointment_choice .o_appointment_avatar_background {
  background-color: #e1e3fa;
}

.o_appointment .o_appointment_svg_fill_color_1, .o_appointment_choice .o_appointment_svg_fill_color_1 {
  fill: #6772E5;
}

.o_appointment select, .o_appointment_choice select {
  background-color: #ffffff;
  color: rgba(33, 37, 41, 0.7);
}

.o_appointment select:focus, .o_appointment_choice select:focus {
  background-color: #ffffff;
  color: #212529;
}

.o_appointment select:disabled, .o_appointment select.o_wysiwyg_loader, .o_appointment_choice select:disabled, .o_appointment_choice select.o_wysiwyg_loader {
  background-color: #d1d5f7;
  color: rgba(33, 37, 41, 0.7);
  pointer-events: none;
  background-image: none;
}

.o_appointment .alert-info a, .o_appointment_choice .alert-info a {
  color: #17a2b8;
}

