
/* /web/static/src/core/model_field_selector/model_field_selector_popover.scss */

.o_popover_field_selector.o-popover-top > .popover-arrow::after {
  border-top-color: #d8dadd;
}

.o_popover_field_selector.o-popover-bottom > .popover-arrow::after {
  border-bottom-color: #d8dadd;
}

.o_model_field_selector_popover {
  width: 265px;
  background-color: #FFF;
  --o-input-background-color: #FFF;
}

.o_model_field_selector_popover:focus {
  outline: none;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header, .o_model_field_selector_popover .o_model_field_selector_popover_footer {
  color: #FFFFFF;
  background: #d8dadd;
  font-weight: bold;
  padding: 5px 0.4em;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header .o_model_field_selector_popover_title, .o_model_field_selector_popover .o_model_field_selector_popover_footer .o_model_field_selector_popover_title {
  width: 100%;
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
  padding: 0px 35px;
  text-align: center;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header .o_model_field_selector_popover_option, .o_model_field_selector_popover .o_model_field_selector_popover_footer .o_model_field_selector_popover_option {
  position: absolute;
  top: 0;
  left: auto;
  bottom: auto;
  right: auto;
  padding: 8px;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header .o_model_field_selector_popover_option.o_model_field_selector_popover_prev_page, .o_model_field_selector_popover .o_model_field_selector_popover_footer .o_model_field_selector_popover_option.o_model_field_selector_popover_prev_page {
  left: 0;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header .o_model_field_selector_popover_option.o_model_field_selector_popover_close, .o_model_field_selector_popover .o_model_field_selector_popover_footer .o_model_field_selector_popover_option.o_model_field_selector_popover_close {
  right: 0;
}

.o_model_field_selector_popover .o_model_field_selector_popover_header .o_model_field_selector_popover_option:hover, .o_model_field_selector_popover .o_model_field_selector_popover_footer .o_model_field_selector_popover_option:hover {
  background: #3b4ade;
}

.o_model_field_selector_popover .o_model_field_selector_popover_body .o_model_field_selector_popover_page {
  position: relative;
  height: 320px;
  overflow: auto;
  margin: 0;
  padding: 0;
}

.o_model_field_selector_popover .o_model_field_selector_popover_body .o_model_field_selector_popover_page > .o_model_field_selector_popover_item {
  list-style: none;
  cursor: pointer;
  font-family: Arial;
  font-size: 13px;
  border-bottom: 1px solid #d6d9dc;
}

.o_model_field_selector_popover .o_model_field_selector_popover_body .o_model_field_selector_popover_page > .o_model_field_selector_popover_item.active {
  background: #E9ECEF;
}

.o_model_field_selector_popover .o_model_field_selector_popover_body .o_model_field_selector_popover_page > .o_model_field_selector_popover_item .o_model_field_selector_popover_item_title {
  font-size: 12px;
}

.o_model_field_selector_popover .o_model_field_selector_popover_body .o_model_field_selector_popover_page > .o_model_field_selector_popover_item .o_model_field_selector_popover_item_relation {
  border-radius: 0px;
  border-left: 1px solid #d6d9dc;
}

