
/* /web/static/src/core/errors/error_dialog.scss */

@media (min-width: 768px) {
  .o_error_dialog {
    padding: 1rem;
  }
  .o_error_dialog .o_error_detail {
    overflow: auto;
  }
  .o_error_dialog .o_error_detail pre {
    overflow: initial;
    max-height: 30vh;
  }
}

.o_error_dialog .modal-header {
  border: none;
  padding-bottom: 0;
  margin-bottom: -1rem;
}

.o_error_dialog .modal-header .modal-title {
  font-size: 1.625rem;
}

.o_error_dialog .modal-footer {
  border: none;
  padding-top: 0;
}

