
/* /web/static/src/legacy/scss/ui.scss */

:root .o_hidden {
  display: none !important;
}

.o_disabled {
  pointer-events: none;
  opacity: 0.5;
}

.o_text_overflow {
  display: inline-block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: top;
}

.ui-autocomplete {
  z-index: 1056;
  max-width: 600px;
}

.ui-autocomplete .ui-menu-item > a {
  display: block;
}

.dropdown-toggle {
  white-space: nowrap;
}

.dropdown-toggle.o-no-caret::before, .dropdown-toggle.o-no-caret::after {
  content: normal;
}

:not(.collapsed) > .o-collapsed-label,
.collapsed > .o-not-collapsed-label {
  display: none;
}

.o_rtl .ui-autocomplete {
  direction: ltr;
  right: 0;
  left: auto;
}

.o_rtl .ui-datepicker-next, .o_rtl .ui-datepicker-prev {
  -webkit-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

.o_catch_attention {
  position: relative;
  z-index: 1;
  animation: catchAttention 200ms ease 0s infinite normal;
}

.o_treeEntry {
  padding-left: var(--treeEntry-padding-h, 1.5rem);
  position: relative;
}

.o_treeEntry:before, .o_treeEntry:after {
  position: absolute;
  left: var(--treeEntry--beforeAfter-left, calc(var(--treeEntry-padding-h, 1.5rem) * .5));
  background: var(--treeEntry--beforeAfter-color, #dee2e6);
  content: '';
}

.o_treeEntry:before {
  top: var(--treeEntry--before-top, 0);
  width: 1px;
  height: 100%;
}

.o_treeEntry:after {
  display: var(--treeEntry--after-display, initial);
  top: calc(.5em + var(--treeEntry-padding-v, 0.5rem));
  width: calc(var(--treeEntry-padding-h, 1.5rem) * .5);
  height: 1px;
}

.o_treeEntry:last-of-type:before {
  height: calc(.5em + var(--treeEntry-padding-v, 0.5rem));
}

@keyframes catchAttention {
  0%, 20%, 40%, 60%, 80%, 100% {
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    transform: translateY(-30%);
  }
  20% {
    transform: translateY(-25%);
  }
  40% {
    transform: translateY(-20%);
  }
  60% {
    transform: translateY(-15%);
  }
  80% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(-5%);
  }
}

span.o_force_ltr {
  display: inline;
}

.o_force_ltr {
  unicode-bidi: embed;
  /*rtl:ignore*/
  direction: ltr;
}

