}

.o_image_24_cover {
  width: 24px;
  height: 24px;
  object-fit: cover;
}

.o_image_40_cover {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

.o_image_64_cover {
  width: 64px;
  height: 64px;
  object-fit: cover;
}

.o_image_64_contain {
  width: 64px;
  height: 64px;
  object-fit: contain;
}

.o_image_64_max {
  max-width: 64px;
  max-height: 64px;
}

.o_image_128_max {
  max-width: 128px !important;
  max-height: 128px !important;
}

.o_width_128 {
