}

.o_web_accesskey_overlay {
  position: absolute;
  top: 0;
  left: auto;
  bottom: auto;
  right: 0;
  display: -webkit-box; display: -webkit-flex; display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  color: #FFFFFF;
  font-size: 1rem;
  font-family: sans-serif;
}

