
/* /knowledge/static/src/scss/knowledge_common.scss */

.o_knowledge_icon_search svg {
  width: var(--oi-font-size, 1em);
  aspect-ratio: 1;
}

.o_knowledge_icon_search:not(:hover) {
  --oi-color: currentColor;
}

.o_knowledge_article_view_form_view .o_form_renderer {
  height: 100% !important;
  padding: 0 !important;
}

.o_knowledge_form_view {
  --knowledge-article-sidebar-size: 300px;
  background-color: var(--knowledge-view-bg, #FFF);
}

.o_knowledge_form_view .o_knowledge_aside {
  width: clamp(200px, var(--knowledge-article-sidebar-size), 576px);
  transition: width 0.15s linear;
}

.o_knowledge_form_view .o_knowledge_aside ul {
  list-style: none;
  padding: 0;
}

.o_knowledge_form_view .o_knowledge_aside li {
  position: relative;
}

.o_knowledge_form_view .o_knowledge_aside li.o_article_has_children > div > .o_article_caret, .o_knowledge_form_view .o_knowledge_aside li.mjs-nestedSortable-branch > div > .o_article_caret {
  visibility: visible !important;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:not(.ui-sortable-helper), .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more:not(.ui-sortable-helper) {
  margin-left: 0.64285715em;
  padding-left: 0.8em;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:not(.ui-sortable-helper):before, .o_knowledge_form_view .o_knowledge_aside li > ul > li:not(.ui-sortable-helper):after, .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more:not(.ui-sortable-helper):before, .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more:not(.ui-sortable-helper):after {
  content: '';
  background-color: #dee2e6;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:not(.ui-sortable-helper):after, .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more:not(.ui-sortable-helper):after {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: auto;
  width: 1px;
  height: 100%;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li.o_article_has_children:before, .o_knowledge_form_view .o_knowledge_aside li > ul > li.mjs-nestedSortable-branch:before, .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more.o_article_has_children:before, .o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more.mjs-nestedSortable-branch:before {
  position: absolute;
  top: 1em;
  left: 0;
  bottom: auto;
  right: auto;
  width: .6em;
  height: 1px;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:before, .o_knowledge_form_view .o_knowledge_aside li > ul > a:not(.o_knowledge_article_load_more_ellipsis):before {
  position: absolute;
  top: 1em;
  left: 0;
  bottom: auto;
  right: auto;
  width: 1.8em;
  height: 1px;
  margin: auto;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:last-child, .o_knowledge_form_view .o_knowledge_aside li > ul > a:not(.o_knowledge_article_load_more_ellipsis):last-child {
  padding-bottom: 0;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > li:last-child:after, .o_knowledge_form_view .o_knowledge_aside li > ul > a:not(.o_knowledge_article_load_more_ellipsis):last-child:after {
  height: 1em;
}

.o_knowledge_form_view .o_knowledge_aside li > ul > a.o_knowledge_article_load_more:not(.ui-sortable-helper) {
  padding-left: 2.5rem;
}

.o_knowledge_form_view .o_knowledge_article_form_resizer {
  position: absolute;
  top: 0;
  left: -0.25rem;
  bottom: 0;
  right: auto;
  cursor: col-resize;
  touch-action: none;
}

.o_knowledge_form_view .o_knowledge_article_form_resizer span {
  position: absolute;
  top: 0;
  left: 0.125rem;
  bottom: 0;
  right: auto;
}

.o_knowledge_form_view .o_knowledge_cover {
  width: 100%;
}

.o_knowledge_form_view .o_knowledge_cover img {
  width: 100%;
  height: 30vh;
  min-height: 150px;
  object-fit: cover;
}

.o_knowledge_form_view .o_knowledge_cover.o_cursor_grab {
  cursor: grab;
}

.o_knowledge_form_view .o_knowledge_cover.o_cursor_grabbing {
  cursor: grabbing;
}

.o_knowledge_form_view .o_knowledge_cover .o_reposition_hint {
  background-color: black;
  color: white;
}

