
/* /website_payment/static/src/snippets/s_donation/000.scss */

.s_donation:not([data-vcss]) input::-webkit-outer-spin-button,
.s_donation:not([data-vcss]) input::-webkit-inner-spin-button {
  -webkit--webkit-appearance: none; -moz-appearance: none; appearance: none;
  margin: 0;
}

.s_donation:not([data-vcss]) input[type=number] {
  -moz--webkit-appearance: textfield; -moz-appearance: textfield; appearance: textfield;
}

.s_donation:not([data-vcss]) .s_donation_btn {
  transition: background 0.2s;
}

.s_donation:not([data-vcss]) .s_donation_btn:focus {
  box-shadow: none !important;
}

.s_donation:not([data-vcss]) #s_donation_amount_input {
  border: none;
  outline: none;
  max-width: 145px;
  background-color: transparent;
  font-size: inherit;
  color: inherit;
}

.s_donation:not([data-vcss]) #s_donation_amount_input::placeholder {
  opacity: 0.6;
  color: inherit;
}

.s_donation:not([data-vcss]) .s_donation_range_slider_wrap {
  width: 90%;
}

.s_donation:not([data-vcss]) .s_donation_range_slider_wrap #s_donation_range_slider {
  margin-bottom: 50px;
}

.s_donation:not([data-vcss]) .s_donation_range_slider_wrap .s_range_bubble {
  position: absolute;
  left: 0%;
  margin-top: 28px;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  background: #6772E5;
  font-size: 1.15rem;
  border-radius: 0.3rem;
  transform: translateX(-50%);
}

.s_donation:not([data-vcss]) .s_donation_range_slider_wrap .s_range_bubble::after {
  content: "";
  position: absolute;
  top: -5px;
  left: 50%;
  width: 0;
  border-style: solid;
  border-color: #6772E5 transparent;
  border-width: 0 5px 5px;
  margin-left: -5px;
}

