
/* /website_appointment/static/src/scss/website_appointment.scss */

.o_appointment_index, .o_wappointment_wrap {
  min-height: calc(98vh - 56px);
}

.o_appointment .o_appointment_details_column {
  min-height: unset;
}

.o_wappointment_appointment_list_columns .card header {
  min-height: 150px;
  height: 25vh;
}

.o_wappointment_appointment_list_columns .card .card-body {
  padding: 1.25rem 1.5rem;
}

.o_wappointment_appointment_list_columns .card .card-body h2, .o_wappointment_appointment_list_columns .card .card-body .h2 {
  font-size: 28px;
}

.o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_location .fa::before, .o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_staff_users .fa::before {
  width: 2rem;
  display: block;
}

.o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_staff_users .o_wappointment_user_avatars {
  height: 32px;
}

.o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_staff_users .o_wappointment_user_avatars .o_wappointment_user_avatar {
  margin-left: -9px;
  height: 32px;
  width: 32px;
  object-fit: cover;
  border: 2px solid #FFFFFF;
}

.o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_staff_users .o_wappointment_user_avatars .o_wappointment_user_avatar:first-child {
  margin-left: 0px;
}

.o_wappointment_appointment_list_columns .card .card-body .o_wappointment_card_staff_users .o_wappointment_user_avatars .o_wappointment_user_avatar:only-child {
  margin-right: 0.5rem;
}

.o_wappointment_appointment_list_columns .card .card-body .navbar-brand {
  bottom: 10px;
  left: 1.5rem;
}

.o_wappointment_appointment_list_columns .card .card-body .navbar-brand:hover {
  color: #52374b !important;
}

.o_wappointment_navbar {
  min-height: 65px;
}

.o_wappointment_navbar .navbar-brand {
  color: inherit;
}

.o_wappointment_navbar .o_wappointment_navbar_link:hover {
  color: #6772E5 !important;
}

.o_wappointment_progress_bar {
  padding: 0px 32px 0px 0px;
  height: 64px;
}

.o_wappointment_progress_bar li {
  border: 1px solid transparent;
  border-right-width: 0;
  padding: 0 32px 0 64px;
  height: 64px;
  line-height: 64px;
}

.o_wappointment_progress_bar li.o_passed_stage:hover {
  background-color: #d1d5f7 !important;
}

.o_wappointment_progress_bar li.o_passed_stage:hover .chevron, .o_wappointment_progress_bar li.o_passed_stage:hover .chevron::before {
  border-left-color: #d1d5f7 !important;
}

.o_wappointment_progress_bar li.o_passed_stage .chevron:before {
  border-left: 32px solid #ffffff;
}

.o_wappointment_progress_bar li.o_current_stage {
  background: #e1e3fa;
}

.o_wappointment_progress_bar li.o_current_stage a {
  cursor: default;
}

.o_wappointment_progress_bar li.o_current_stage .chevron:before {
  border-left: 32px solid #e1e3fa;
}

.o_wappointment_progress_bar li.o_future_stage a {
  cursor: default;
}

.o_wappointment_progress_bar li.o_future_stage .chevron:before {
  border-left: 32px solid #ffffff;
}

.o_wappointment_progress_bar li:first-child {
  padding-left: 32px;
}

.o_wappointment_progress_bar li:last-child .chevron {
  background-color: transparent;
}

.o_wappointment_progress_bar li .chevron {
  top: -1px;
  right: -32px;
  z-index: 1;
  border: 32px solid transparent;
  border-right: 0;
  border-left: 32px solid #d1d5f7;
}

.o_wappointment_progress_bar li .chevron:before {
  position: absolute;
  top: -32px;
  right: 1px;
  display: block;
  border: 32px solid transparent;
  border-right: 0;
  content: "";
}

.o_wappointment_select_operator .o_wappointment_select_btn {
  border: 1px solid #6772E5;
}

.o_wappointment_select_operator .o_wappointment_select_btn:hover {
  background-color: #6772E5;
  color: white !important;
  text-decoration: none;
}

.o_wappointment_select_operator .card header {
  height: 300px;
}

.o_wappointment_select_operator .card .card-body {
  padding: 1.25rem;
}

.o_wappointment_select_operator .card .card-body .o_wappointment_card_user_dsc {
  color: rgba(0, 0, 0, 0.42) !important;
}

.o_wappointment_select_operator .card .card-body .o_wappointment_card_user_select {
  bottom: 1rem;
  width: calc(100% - 2 * 1.25rem);
}

