
/* /web/static/src/core/dropdown/dropdown.scss */

.o-dropdown {
  /*!rtl:begin:ignore*/
  /*!rtl:end:ignore*/
}

.o-dropdown--menu {
  position: fixed;
  margin-top: var(--Dropdown_menu-margin-y, 0.25rem);
  margin-bottom: var(--Dropdown_menu-margin-y, 0.25rem);
}

.o-dropdown--menu.o-dropdown--menu-submenu {
  --Dropdown_menu-margin-y: 0;
}

.o-dropdown--menu .dropdown-toggle:focus, .o-dropdown--menu .dropdown-item:focus {
  background-color: transparent;
  outline: none;
}

.o-dropdown--menu .dropdown-toggle.focus, .o-dropdown--menu .dropdown-item.focus {
  background-color: #E9ECEF;
}

.o-dropdown--menu .dropdown-item:not(.disabled):not(:disabled):not(.o_wysiwyg_loader), .o-dropdown--menu .dropdown-item:not(.disabled):not(:disabled):not(.o_wysiwyg_loader) label {
  cursor: pointer;
}

.o-dropdown--menu .o-dropdown > .o-dropdown--menu {
  --o-dropdown--submenu-margin: calc(-.5rem - 1px);
  margin-top: var(--o-dropdown--submenu-margin);
  margin-bottom: var(--o-dropdown--submenu-margin);
}

.o-dropdown.dropup > .o-dropdown--menu,
.o-dropdown.dropdown > .o-dropdown--menu,
.o-dropdown.dropstart > .o-dropdown--menu,
.o-dropdown.dropend > .o-dropdown--menu {
  left: auto;
  right: auto;
  margin-left: 0;
  margin-right: 0;
}

.o-dropdown--no-caret > .dropdown-toggle::before, .o-dropdown--no-caret > .dropdown-toggle::after {
  content: normal;
}

.o-dropdown button.dropdown-toggle.active, .o-dropdown button.dropdown-toggle:hover, .o-dropdown button.dropdown-toggle:focus, .o-dropdown button.dropdown-toggle:active {
  outline: none;
  box-shadow: none !important;
}

.o-dropdown button.dropdown-toggle.dropdown-item:not(.o_menu_item)::after, .o-dropdown button.dropdown-toggle.dropdown-item:not(.o_menu_item)::before {
  position: absolute;
  top: 0;
  left: auto;
  bottom: auto;
  right: 0;
  transform: translate(-0.6em, 0.6em) /*rtl:translate(.6em, .6em) scaleX(-1)*/;
}

