
/* /web/static/src/core/debug/profiling/profiling_qweb.scss */

.o_form_view .o_ace_view_editor {
  background: transparent;
}

.o_profiling_qweb_view {
  user-select: none;
}

.o_profiling_qweb_view .o_select_view_profiling {
