.o_profiling_qweb_view .ace_editor .o_detail {
  position: absolute;
  z-index: 1;
  background: #ffedcb;
  color: orange !important;
  border: 1px orange solid;
  padding: 6px;
  white-space: normal;
  text-align: right;
}

